@import 'reset-css/sass/_reset.scss';

* {
  box-sizing: border-box;
}
body {
  min-width: 1200px;
  font-family: 'PingFangSC-Regular', 'Microsoft YaHei', Arial, sans-serif;
  font-size: 14px;
  color: #333333;
}

img {
  display: block;
}

a {
  text-decoration: none;
  outline: none;
}

.container {
  width: 1200px;
  margin: 0 auto;
}

.opa-7 {
  color:rgba(255, 255, 255, .7);
}