.about-us {
  .banner {
    height: 385px;
    background: url('../../assets/images/about/about-banner.png') no-repeat bottom center / cover;

    .container {
      padding: 0 60px 0 20px;
      height: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;

      .bnr-title {
        width: 432px;
        font-size: 36px;
        margin-bottom: 36px;
        line-height: 50px;
        color: #fff;
      }

      .bnr-cont {
        color: #fff;
      }

      .banner-right {
        width: 320px;
        height: 100%;
        background: url('../../assets/images/about/banner-right.png') no-repeat center bottom / contain;
      }
    }
  }

  .comp-msg {
    text-align: center;

    .title {
      font-size: 30px;
      color: #2b2932;
      margin: 52px auto 28px;
    }

    .content {
      width: 920px;
      color: #666666;
      line-height: 20px;
      margin: auto;
    }

    .comp-boxes {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin: 40px auto 50px;

      .compmsg-box {
        width: 140px;
        height: 120px;
        background-color: #ccc;

        .nums {
          font-size: 56px;
          color: #103afa;
          line-height: 68px;
          position: relative;

          .words-top {
            display: inline-block;
            font-size: 28px;
            transform: translateY(-22px);
          }

          .words-btm {
            display: inline-block;
            font-size: 22px;
          }
        }

        .msg-title {
          font-size: 16px;
        }
      }
    }
  }

  .history {
    height: 480px;
    overflow: hidden;
    background: #f7faff url('../../assets/images/about/hisbg.png') no-repeat center;
    background-size: contain;
    text-align: center;

    .title {
      font-size: 30px;
      color: #2b2932;
      margin: 70px auto 38px;
    }

    .content {
      width: 920px;
      color: #666666;
      line-height: 20px;
      margin: 0 auto 62px;
    }

    .his-boxes {
      display: flex;
      justify-content: center;
      align-items: flex-start;

      .icon-point {
        display: block;
        width: 24px;
        height: 24px;
        background: url('../../assets/images/about/list2.png') no-repeat center;
        background-size: contain;
        position: relative;
        margin-bottom: 20px;
      }

      .his-box {
        width: 215px;
        height: 90px;
        margin-right: 12px;
        text-align: left;

        .icon-point {
          &.active {
            background: url('../../assets/images/about/list1.png') no-repeat center;
          }

          &::after {
            content: '';
            position: absolute;
            width: 180px;
            height: 2px;
            background-color: #bcc8fc;
            top: 11px;
            right: -190px;
          }
        }

        .word-year {
          font-size: 16px;
          color: #103afa;
          line-height: 26px;
        }

        .word-content {
          color: #666;
        }
      }
    }
  }

  .certificate {
    .title {
      font-size: 30px;
      color: #2b2932;
      margin: 60px auto 32px;
      text-align: center;
    }

    .cert-box {
      display: inline-block;
      width: 342px;
      height: 333px;
      border-radius: 2px;
      margin: 28px 28px 88px;
      padding-top: 161px;
      vertical-align: top;

      .cert-main {
        height: 100%;
        background: #f7faff;
        border: 2px solid #e8eff9;
        border-top: 0;
        padding: 24px 30px 20px;

        .cert-title {
          font-size: 14px;
          line-height: 34px;
          color: #000000;
        }

        .cert-content {
          font-size: 12px;
          line-height: 20px;
          color: #666666;
        }
      }
    }
  }

  .location-tel {
    height: 373px;
    background: url('../../assets/images/about/address.png') no-repeat center;
    background-size: cover;
    margin-bottom: 4px;
    .container {
      overflow: hidden;
      padding-left: 15px;

      .loc-title {
        font-size: 30px;
        color: #2b2932;
        margin: 54px 0 30px;
      }

      .line-center {
        display: inline-block;
        width: 1px;
        height: 115px;
        background-color: #d8d8d8;
        margin-right: 70px;
      }

      .tel-address {
        display: flex;
        align-items: center;

        .loc-box {
          width: 260px;
        }

        .tel-num {
          font-family: Avenir-BookOblique;
          font-size: 30px;
          color: #333333;
          transform: skew(3-18deg);
          line-height: 40px;
        }

        .word-333 {
          font-size: 16px;
          color: #333333;
          line-height: 22px;
          margin-top: 28px;
          margin-bottom: 8px;
        }

        .word-ccc {
          color: #888888;
          line-height: 20px;
        }

        button {
          width: 160px;
          height: 40px;
          line-height: 40px;
          margin: 10px auto;
          background: #103afa;
          border-radius: 4px;
          color: #fff;
          padding-left: 42px;
          position: relative;
          border: 0;
          &::before {
            content: '';
            position: absolute;
            width: 26px;
            height: 24px;
            top: 7px;
            left: 29px;
            background: url('../../assets/images/about/chat.png') no-repeat center;
            background-size: cover;
          }
        }
      }
    }
  }
}
