.document {
  background: linear-gradient(to right, #F5F5F5 50%, #fff 50%);
  .container {
    min-height: 500px;
    display: flex;
    color: #333;
    .side {
      width: 230px;
      padding: 50px 30px 50px 10px;
      .title {
        font-size: 20px;
      }
      .navs {
        margin-top: 20px;
        .nav {
          line-height: 45px;
          font-size: 14px;
          border-bottom: 1px dashed #DAD7D7;
          cursor: pointer;
          &.active {
            color: #01C7FE;
          }
        }
      }
    }
    .main {
      width: calc(100% - 230px);
      background-color: #fff;
      .inner {
        width: 100%;
        height: 100%;
        padding: 30px;
      }
      .title {
        font-size: 24px;
        margin-bottom: 30px;
        font-weight: bold;
      }
      .content {
        img {
          width: 80%;
          margin: 0 auto;
          &.tal {
            margin: initial;
          }
        }
        h2 {
          white-space: pre-wrap;
          font-size: 20px;
          font-weight: bold;
          margin-top: 25px;
          margin-bottom: 15px;
        }
        h3 {
          white-space: pre-wrap;
          font-size: 16px;
          font-weight: bold;
          margin-top: 20px;
          margin-bottom: 15px;
        }
        h4 {
          white-space: pre-wrap;
          font-size: 14px;
          font-weight: bold;
          margin-top: 10px;
          margin-bottom: 10px;
        }
        p {
          white-space: pre-wrap;
          font-size: 14px;
          line-height: 28px;
        }
        blockquote {
          font-weight: bold;
        }
        .img-descr {
          text-align: center;
          font-size: 12px;
          margin: 1em 0;
        }
        ul, ol {
          list-style: initial;
          li {
            margin: 1em 1.25em;
            line-height: 1.5em;
            img {
              margin: initial;
            }
          }
        }
        code, pre {
          color: black;
          background: none;
          text-shadow: 0 1px white;
          font-family: Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
          font-size: 1em;
          text-align: left;
          word-break: break-all;
          word-spacing: normal;
          word-wrap: break-word;
          white-space: pre-wrap;
          line-height: 1.5;
          tab-size: 4;
          hyphens: none;
        }
        pre {
          padding: 1.5em 3em 1.5em 1em;
          margin: .5em 0;
          overflow: auto;
          border: 1px solid #DDD;
          border-radius: 4px;
          background: #F7F7F7;
        }
      }
    }
  }
}