.solution {
    .banner {
        height: 385px;
        background: url("../../assets/images/Solution/banner-solution.png") no-repeat bottom center;
        background-size: cover;
        display: flex;
        justify-content: center;
        align-items: center;

        .container {
            .bnr-title {
                font-size: 42px;
                margin-bottom: 42px;
                color: #fff;
                text-align: center;
            }

            .bnr-cont {
                font-size: 16px;
                color: #01C7FE;
                text-align: center;
            }
        }
    }
	
	.connect-now {
		background: url('../../assets//images/Solution/connect.png') no-repeat center;
		background-size: cover;
		height: 127px;
		.ad {
			width: 900px;
			height: 127px;
			margin:auto;
			display: flex;
			justify-content: space-between;
			align-items: center;
			p {
				font-size: 20px;
				color: #fff;
			}
			button {
				 width: 122px;
				 height: 38px;
				 background-color: #FFAA00;
				 border: 0;
				 border-radius: 2px;
			}
		}
	}
}
