.footer {
  // pointer-events: none;
  .footer-top {
    height: 277px;
    background: #112156;

    .container {
      padding-top: 50px;
      display: flex;
      height: 227px;

      .opa-7 {
        line-height: 36px;
      }

      .ft-left {
        width: 342px;
        margin-top: 30px;

        .logo-btm {
          display: block;
          width: 120px;
          height: 34px;
          margin-bottom: 15px;
          background: url("../../assets/images/logo-btm.png") no-repeat center / contain;
        }
      }

      .ft-center {
        width: 690px;
        display: flex;

        .cen-item {
          margin-right: 110px;

          &:last-child {
            margin-right: 0;
          }

          .title {
            color: #fff;
            margin-bottom: 20px;
            position: relative;

            &::after {
              content: "";
              position: absolute;
              width: 41px;
              left: 0;
              bottom: -10px;
              border-bottom: 3px solid #103afa;
            }
          }

          .nav {
            .icon-tel {
              pointer-events: none;
              margin-left: 26px;
              position: relative;
              &::after {
                position: absolute;
                content: "";
                width: 20px;
                height: 20px;
                left: -26px;
                top: 0;
                background: url("../../assets/images/icon-tel.png") no-repeat
                  center / contain;
              }
            }

            .icon-email {
              pointer-events: none;
              margin-left: 26px;
              position: relative;
              &::after {
                position: absolute;
                content: "";
                width: 20px;
                height: 20px;
                left: -26px;
                top: -2px;
                background: url("../../assets/images/icon-email.png") no-repeat
                  center / contain;
              }
            }
          }
        }
      }

      .ft-right {
        width: 148px;
        margin: 6px 20px 0 0;
        background: url("../../assets/images/footer-weixin.png") no-repeat
          center top / contain;
        .weixin {
          margin-top: 160px;
          color: #fff;
          text-align: center;
        }
      }
    }
  }

  .footer-btm {
    height: 54px;
    line-height: 54px;
    background: #0a163d;
  }
}
