.production {
  .banner {
    height: 380px;
    padding-top: 60px;
    color: #fff;
    font-size: 30px;
    text-align: center;
    background:
      url('../../assets/images/production/banner-1.png') no-repeat center bottom / 1099px 199px,
      url('../../assets/images/production/banner-bg.png') no-repeat center bottom / cover;
  }
  .history-data, .support {
    background-color: #F7FAFF;
  }
  .support .container {
    padding: 60px 0;
    > .title {
      text-align: center;
      font-size: 28px;
      margin-bottom: 25px;
      color: #2B2932;
    }
    .content.items {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      &:after{
        content: "";
        width: 32%;
        display: block;
        height:0;
      }
      > div:not(:nth-child(n+4)) {
        margin-bottom: 20px;
      }
    }
  }
}