.block-introduce {
    margin: 40px auto 55px;

    .container {
        display: flex;
        .half-img {
            flex: 2;
            .block-box {
                width: 580px;
                height: 175px;
            }
        }

        .quater-bg {
            flex: 1;

            .block-box {
                width: 280px;
                height: 175px;
            }
        }

        .block-box {
            margin: 10px;
            height: 130px;
            color: #fff;
            padding: 35px 22px 10px;
            word-wrap: break-word;

            .title {
                line-height: 48px;
                font-size: 18px;
            }

            .content {
                line-height: 20px;
            }
        }
    }
}
