.example {
    text-align: center;
    background: #F7FAFF;
    height: 838px;
    padding: 48px 0 50px;

    .exp-title {
        font-size: 30px;
        margin-bottom: 38px;
    }

    .exp-cell {
        display: inline-block;
        width: 166px;
        border-bottom: 4px solid transparent;
        margin: 0 32px;
        &:hover {
            cursor: pointer;
        }
        &.active {
            border-bottom-color: #103AFA;
        }
        .icon-cell {
            display: block;
            width: 61px;
            height: 61px;
            margin: auto;
            background-color: #ccc;
        }

        .title {
            font-size: 16px;
            line-height: 22px;
            margin-bottom: 27px;
        }
    }

    .container {
        height: 570px;
        background-color: #fff;
        box-shadow: 0 4px 10px 0 rgba(5, 40, 80, 0.06);
        text-align: left;
        padding: 26px;

        .exp-img {
            display: inline-block;
            width: 702px;
            height: 518px;
            margin-right: 40px;
        }

        .exp-content {
            display: inline-block;
            width: 390px;
            height: 518px;
            vertical-align: top;

            .title-cont {
                .title {
                    font-size: 16px;
                    color: #2B2932;
                    margin: 22px 0 29px;
                }

                .content {
                    color: #888;
                    line-height: 20px;
                    margin-bottom: 17px;
                }
            }

            .point-cont {
                margin-top: 22px;

                .content {
                    line-height: 34px;
                    color: #2B2932;
                    margin-left: 30px;
                    position: relative;

                    &::after {
                        content: '';
                        position: absolute;
                        width: 14px;
                        height: 14px;
                        background-color: #ccc;
                        left: -30px;
                        top: 10px;
                        background-image: linear-gradient(132deg, #fff 5%, rgb(172, 189, 240) 100%);
                        border-radius: 50%;
                    }
                }
            }
        }
    }
}
