.carousel-wrapper {
  position: relative;
  background-color: #112156;
  .carousel {
    width: 1200px;
    margin: 0 auto;
    .item {
      height: 500px;
      .container {
        position: relative;
        height: 100%;
        .info {
          position: absolute;
          top: 120px;
          color: #fff;
          .title {
            font-size: 40px;
            margin-bottom: 30px;
          }
          .descr {
            font-size: 16px;
            color: #02DAFE;
            line-height: 1.8;
          }
          .more {
            display: inline-block;
            color: #fff;
            text-align: center;
            margin-top: 40px;
            width: 160px;
            line-height: 44px;
            background: #3510FA;
            border-radius: 1px;
            cursor: pointer;
          }
        }
        .banner-img {
          position: absolute;
          left: calc(50% - 50px);
          bottom: 44px;
          img {
            height: 428px;
          }
        }
      }
    }
  }
  .indicator.items {
    position: absolute;
    left: 50%;
    bottom: 40px;
    margin-left: -70px;
    width: 140px;
    height: 4px;
    font-size: 0;
    .item {
      display: inline-block;
      width: 40px;
      height: 100%;
      background-color: rgba(255, 255, 255, .3);
      &.active {
        background-color: #fff;
      }
      &:not(:last-child) {
        margin-right: 10px;
      }
    }
  }
}