.header {
  padding: 30px 0 22px 0;
  background: #112156;
  .container {
    display: flex;
    justify-content: space-between;
    .logo {
      width: 170px;
      height: 48px;
      background-color: #ccc;
      background: url("../../assets/images/logo.png") no-repeat center / contain;
    }
    .nav {
      font-size: 0;
      .nav-item {
        display: inline-block;
        position: relative;
        &:not(:first-child) {
          margin-left: 20px;
        }
        .link {
          font-size: 14px;
          line-height: 48px;
          padding: 0 5px;
          color: #fff;
          &.active::after {
            content: "";
            position: absolute;
            width: 42px;
            left: 0;
            right: 0;
            margin: auto;
            bottom: 2px;
            border-bottom: 4px solid #01c7fe;
          }
        }
      }
    }
  }
}
